<template>
    <views class="article">
        <loading
            v-if="loadingTab"
            color="#1989fa">
        </loading>
        <views v-else>
            <nav-bar
                :title="this.collected ? '我的收藏' : moduleTitle"
                :left-arrow="!!collected"
                @click-left="onClickLeft"></nav-bar>
            <search-bar
                :path="path"
                :querys="querys"></search-bar>
            <tab
                :tabs="tabs"
                :active-name="moduleTitle"
                class="mb"
                @change="handleChange">
            </tab>
            <banner :list="banners"></banner>
            <article-list
                ref="articleList"
                v-if="activeTab"
                :collected="collected"
                :keyword="keyword"
                :category="activeTab">
            </article-list>
            <!-- <van-pull-refresh
                v-model="isRefresh"
                success-text="刷新成功"
                @refresh="onRefresh">
            </van-pull-refresh> -->
        </views>
    </views>
</template>

<script>
import NavBar from '@/components/navBar'
import Loading from '@/components/loading'
import SearchBar from '@/components/searchBar'
import Banner from '@/components/banner'
import Tab from '@/components/tab'
import ArticleList from './ArticleList'
import tools from '@/mixins/tools'
import api from '@/utils/api'
export default {
    name: 'Article',
    mixins: [tools],
    components: {
        NavBar,
        SearchBar,
        Tab,
        ArticleList,
        Loading,
        Banner
    },
    data() {
        return {
            isRefresh: false,
            tabs: [],
            banners: [],
            lists: [],
            activeTab: {},
            keyword: '',
            loadingTab: false
        }
    },
    computed: {
        moduleTitle() {
            return this.activeTab.name
        },
        path() {
            return `to=${this.collected ? 'collected' : 'article'}&categoryId=${this.activeTab.id}&categoryName=${this.activeTab.name}`
        },
        querys() {
            return this.$route.query || {}
        },
        collected() {
            return location.pathname === '/collected'
        }
    },
    watch: {
        'querys.keyword': {
            handler(val) {
                this.keyword = val || ''
                this.activeTab = {
                    id: this.querys.categoryId,
                    name: this.querys.categoryName
                }
            }
        }
    },
    created() {
        this.init()
    },
    methods: {
        init() {
            this.getCategory()
            this.getBanner()
        },
        onRefresh() {
            setTimeout(() => {
                this.isRefresh = false
            }, 1000)
        },
        onClickLeft() {
            this.push('/person')
        },
        handleChange(val) {
            this.activeTab = val
            this.keyword = this.querys.keyword
        },
        handleSearch(val) {
            this.$refs.articleList.reload(val)
        },
        getCategory() {
            this.loadingTab = true
            api.getCategory().then((res) => {
                // this.tabs = res.list
                this.tabs = [
                    {
                        id: '111',
                        name: '农业资讯'
                    },
                    {
                        id: '222',
                        name: '种植知识'
                    },
                    {
                        id: '333',
                        name: '产品介绍'
                    }
                ]
                if (this.tabs && this.tabs.length) {
                    this.activeTab = this.tabs[0]
                }
                this.loadingTab = false
            }).catch(() => {
                this.loadingTab = false
            })
        },
        getBanner() {
            api.getCategoryBanner({ category_id: this.activeTab.id }).then((res) => {
                // this.banners = res.list
                this.banners = [
                    {
                        url: require('@/assets/images/article_banner.png')
                    },
                    {
                        url: require('@/assets/images/article_banner.png')
                    }
                ]
            })
        }
    }
}
</script>

<style lang="scss" scoped>
@import './index.scss';
</style>
