<template>
    <views
        :class="{'padding': padding}"
        class="banner">
        <van-swipe
            :autoplay="3000"
            :class="{'radius': radius}"
            class="my-swipe"
            indicator-color="white">
            <van-swipe-item
                v-for="(item, index) in list"
                :key="index">
                <images :src="item.url"></images>
            </van-swipe-item>
        </van-swipe>
    </views>
</template>

<script>
export default {
    props: {
        list: {
            type: Array,
            default: () => []
        },
        padding: {
            type: Boolean,
            default: true
        },
        radius: {
            type: Boolean,
            default: true
        }
    }
}
</script>

<style lang="scss" scoped>
@import './index.scss';
</style>
