<template>
    <van-list
        v-model="loadingData"
        :finished="finished"
        finished-text="没有更多了"
        class="article-list"
        @load="onLoad">
        <article-item
            v-for="(item, index) in list"
            :key="index"
            :item="item"
            @click="handleDetail">
        </article-item>
    </van-list>
</template>

<script>
import ArticleItem from '@/components/articleItem'
import tools from '@/mixins/tools'
import api from '@/utils/api'
export default {
    mixins: [tools],
    components: {
        ArticleItem
    },
    props: {
        category: {
            type: Object,
            default: () => ({})
        },
        keyword: String,
        collected: Boolean
    },
    computed: {
        categoryId() {
            return this.category.id
        },
        categoryName() {
            return this.category.name
        }
    },
    watch: {
        categoryId(n, o) {
            if (n) {
                this.reload()
            }
        },
        keyword: {
            handler(val) {
                this.reload()
            },
            immediate: true
        }
    },
    data() {
        return {
            loadingData: false,
            finished: false,
            list: []
        }
    },
    methods: {
        handleDetail(row) {
            this.push(`/article-detail?id=${row.id}&name=${this.categoryName}`)
        },
        reload() {
            this.list = []
            this.finished = false
            this.loadingData = true
            this.onLoad()
        },
        onLoad() {
            const params = {
                category_id: this.categoryId,
                page: 1,
                page_size: 10,
                collected: this.collected
            }
            if (this.keyword) params.name = this.keyword
            api.getArticleList(params).then((res) => {
                if (res.list.length > 0) {
                    res.list.forEach((item) => {
                        this.list.push({ ...item })
                    })
                    this.loadingData = false
                } else {
                    this.finished = true
                }
            })
        }
    }
}
</script>

<style lang="scss" scoped>
@import './index.scss';
</style>
